import server from '../../server.js';
export default {
	data() {
		return {
			list:[],
			plnr:'',
			huifuid:0,
			setvar:'',
			pluid:0,
			InputBottom: 0,
			postid:0
		}
	},
	onLoad:function(e){
		console.log(e)
		this.huifuid=e.CommentsId;
		this.postid=e.postId
		this.getpl(e.CommentsId,e.postId)
	},
	methods: {
		getbieren:function(e){
			if(e==server.userinfo.Auid){
				uni.navigateTo({
					url: '../i/i'
				})
			}else{
				uni.navigateTo({
					url: '../i/bieren?auid='+e
				})
			}
		},
		sendpl:function(e){
			console.log(this.plnr);
			if(this.plnr=="null" ||this.plnr==""){
				uni.showToast({
					title: "请输入评论内容！",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.plnr.length>300 || this.plnr.length<1 ){
				uni.showToast({
					title: "内容最少1个字，最多300字",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.huifuid && this.huifu==true){
				//回复评论模式
				//console.log("回复评论",this.plnr)
				let qian=this.plnr.substring(0,this.plnr.indexOf(':')+1);
				let nr=this.plnr.replace(qian,"");
				uni.showLoading({
					title: '正在提交数据'
				});
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=hfpl', //仅为示例，并非真实接口地址。
					data: {
						id: this.huifuid,
						txt:nr,
						postid:this.postid,
						uid:this.pluid,
						auid:server.userinfo.Auid
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.setvar="";
							this.plnr=''
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									this.plnr==""
									console.log(this.pllist);
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						
						setTimeout(function () {
							uni.hideLoading()
						}, 2000);
					},
					complete() {
						uni.hideLoading();
					}
				});
				
			}else{
				//评论模式
				uni.showLoading({
					title: '正在提交数据'
				});
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=pl', //仅为示例，并非真实接口地址。
					data: {
						auid: server.userinfo.Auid,
						txt:this.plnr,
						postid:this.postid,
						uid:this.postInfo.AuthorId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.plnr=''
							this.setvar="";
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									console.log(this.pllist);
									this.plnr==""
									
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						setTimeout(function () {
							uni.hideLoading()
						}, 2000);
					},
					complete() {
						uni.hideLoading()
					}
				});
			}
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		pinglun:function(e){
			this.plnr=e.target.value;
			if(this.plnr.indexOf('@')==0){
				this.huifu=true;
			}else{
				this.huifu=false;
			}
		},
		getpl:function(CommentsId,postId){
			uni.showLoading({
				title: '正在请求服务器'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=getdupl&CommentsId="+CommentsId,
				data: {},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.plnr=res.data.data
						this.getList(postId,CommentsId,res.data.data.userinfo.Auid)
						this.$forceUpdate()
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
			
		},
		huifua:function(n,plid,uid){
			this.setvar="@"+n+": ";
			this.huifu=true;
			this.pluid=uid;//父级评论id
			console.log("回复",n,uid)
		},
		getList:function(postid,CommentsId,pluid){
			
			uni.showLoading({
				title: '正在请求服务器'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=gethflist&CommentsId="+CommentsId+"&postid="+postid,
				data: {
					'pluid':pluid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.list=res.data.data
						this.$forceUpdate()
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
			
		}
	}
}

